@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@700&family=Raleway:wght@300;400&family=Roboto:ital,wght@0,400;0,500;0,700;1,400&display=swap');
@layer base{
    body{
        @apply font-[Raleway] 
    }
    li{
        @apply px-4;
        @apply cursor-pointer;

    }
}
.content-div{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height:200px;  
}

.content-div:hover{
    background-image:linear-gradient(
        to right,rgba(112,157,255,0.8),hsla(242,74%,61%,0.8)
    )!important;
}